import { useState, useCallback } from "react";
import { GoogleMap, useJsApiLoader, InfoWindowF, MarkerF } from '@react-google-maps/api';

export default function Map(event) {

    const {
        data
    } = event;

    const [map, setMap] = useState(null);
    const [activeMarker, setActiveMarker] = useState(null);

    const center = {
        lat: 41.44268924491985,
        lng: -81.6569009028065
    };

    const onLoad = useCallback(function callback(map) {
        setMap(map)
    }, [])

    const onUnmount = useCallback(function callback(map) {
        setMap(null)
    }, [])

    const handleActiveMarker = (marker) => {
        if (marker === activeMarker) {
            return;
        }
        setActiveMarker(marker);
    };

    const { isLoaded } = useJsApiLoader({
        id: 'google-map-script',
        googleMapsApiKey: "AIzaSyAStB34xAb3Gcz_Oh2K_sUYSb8L7-6TBH8"
    })

    return isLoaded ? (
        <GoogleMap
            id="map"
            center={center}
            zoom={11}
            onLoad={onLoad}
            onUnmount={onUnmount}
        >
            {
                data.slice(2).map((item) => {
                    const id = item.municipality + item.ward + item.precinct;
                    return <MarkerF
                        position={item.latLng}
                        title={item.municipality + " " + item.ward + item.precinct}
                        key={id}
                        onClick={() => handleActiveMarker(id)}
                    >
                        {activeMarker === id ? (
                            <InfoWindowF
                                disableAutoPan
                                onCloseClick={() => setActiveMarker(null)}
                            >
                                <div>
                                    <p>{item.municipality + " " + item.ward + item.precinct}</p>
                                    <p>{item.name} {item.lastName}</p>
                                    <p>{item.address}</p>
                                </div>
                            </InfoWindowF>
                        ) : null}

                    </MarkerF>
                })
            }
        </GoogleMap>
    ) : <></>;
}